import {
  useSubjectTabControllerMutation,
  useSubjectTabManageQuery,
  useSubjectAddCatalogStudentMutation,
  useSubjectRemoveCatalogStudentMutation,
} from "../../../Services/SubjectManager/subjectManagerApiDynamic";

export const useSubjectTabManage = (args) => {
  const { data, refetch, isFetching } = useSubjectTabManageQuery(args.data, {
    skip: args.skip,
  });
  return {
    subjectTabManage: data,
    subjectTabManageRefetch: refetch,
    subjectTabManageLoading: isFetching,
  };
};

export const useSubjectTabController = () => {
  const [subjectTabController] = useSubjectTabControllerMutation();
  return [subjectTabController];
};

export const useSubjectAddCatalogStudent = () => {
  const [subjectAddCatalogStudent] = useSubjectAddCatalogStudentMutation();
  return [subjectAddCatalogStudent];
};
export const useSubjectRemoveCatalogStudent = () => {
  const [subjectRemoveCatalogStudent] =
    useSubjectRemoveCatalogStudentMutation();
  return [subjectRemoveCatalogStudent];
};
