import { DELETE, PATCH } from "../UrlConfig/MethodType";

export const subjectTabManageFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v1/subject/tab/manage/${data?.did}/query?flow=${data?.flow ?? ""}`,
    providesTags: ["Tab_Manage"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const subjectTabControllerFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/subject/tab/manage/${data?.did}/query`,
      method: PATCH,
      body: data.tabManage,
    }),
    invalidatesTags: ["Tab_Manage"],
  });
};

export const subjectAddCatalogStudentFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/edit/student/${data?.sid}/catalog/add/remove/query`,
      method: PATCH,
      body: data.bodyParameter,
    }),
  });
};

export const subjectRemoveCatalogStudentFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/edit/student/${data?.sid}/catalog/add/remove/query`,
      method: DELETE,
      body: data.bodyParameter,
    }),
  });
};
